y: flex;\n  justify-content: center;\n}\n.header .nav-bar .left-nav .completed-puzzles-box {\n  position: relative;\n  z-index: 10;\n  width: 100%;\n  justify-content: center;\n  display: flex;\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-button {\n  font-weight: 600;\n  background-color: #e6cacf;\n  color: #2f4a62;\n  width: fit-content;\n  border: 2px outset #e6cacf;\n  padding: 0.5rem;\n  border-radius: 5px;\n  box-shadow: 0.2rem 0.2rem 0.2rem #2f4a62;\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-button.unpressed {\n  border-style: outset;\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-button.pressed {\n  border-style: inset;\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-detail {\n  position: absolute;\n  background-color: #e6cacf;\n  top: 4rem;\n  width: 80%;\n  text-align: left;\n  border-radius: 5px;\n  box-shadow: 2px 2px 2px #2f4a62;\n  display: flex;\n  flex-wrap: wrap;\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-detail .solved {\n  margin: 2rem;\n  width: 4rem;\n  height: 4rem;\n  border: 1px solid black;\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-detail .unsolved {\n  margin: 2rem;\n  width: 4rem;\n  height: 4rem;\n  background-color: grey;\n  border: 2px solid black;\n  background-image: url(../../images/question_mark.png);\n  background-size: 4rem 4rem;\n  background-position: center;\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-detail #tutorial.solved {\n  background-image: url(../../images/tutorial.png);\n  background-size: 4rem 4rem;\n  background-position: center;\n  border-bottom: none;\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-detail #tutorial.solved:hover {\n  filter: brightness(120%);\n}\n.header .nav-bar .left-nav .completed-puzzles-box #completed-puzzles-detail #level_one.solved {\n  background-image: url(../../images/le