v-bar .left-nav .completed-puzzles-box #completed-puzzles-detail.active {\n  display: flex;\n}\n.header .nav-bar .instructions-box {\n  width: 40%;\n  z-index: 10;\n  display: flex;\n  justify-content: center;\n  position: relative;\n}\n.header .nav-bar .instructions-box #instructions-button {\n  font-weight: 600;\n  background-color: #e6cacf;\n  color: #2f4a62;\n  width: fit-content;\n  border: 2px outset #e6cacf;\n  padding: 0.5rem;\n  border-radius: 5px;\n  box-shadow: 0.2rem 0.2rem 0.2rem #2f4a62;\n}\n.header .nav-bar .instructions-box #instructions-button:hover {\n  cursor: pointer;\n}\n.header .nav-bar .instructions-box #instructions-button.unpressed {\n  border-style: outset;\n}\n.header .nav-bar .instructions-box #instructions-button.pressed {\n  border-style: inset;\n}\n.header .nav-bar .instructions-box #instructions-detail {\n  position: absolute;\n  background-color: #e6cacf;\n  top: 4rem;\n  width: 80%;\n  text-align: left;\n  border-radius: 5px;\n  box-shadow: 2px 2px 2px #2f4a62;\n}\n.header .nav-bar .instructions-box #instructions-detail ul {\n  padding: 0 1rem;\n  margin: 1rem 2rem;\n}\n.header .nav-bar .instructions-box #instructions-detail p {\n  padding: 0 1rem;\n  margin: 1rem 2rem;\n}\n.header .nav-bar .instructions-box #instructions-detail p span {\n  font-weight: 600;\n}\n.header .nav-bar .instructions-box .hidden {\n  display: none;\n  height: 0;\n  width: 400px;\n}\n.header .nav-bar .instructions-box .active {\n  display: block;\n  height: fit-content;\n}\n\n.board-container {\n  background-color: #5481bd;\n  box-shadow: 2px 2px 2px #2f4a62;\n  border-radius: 20%;\n  width: fit-content;\n  padding: 0 6em;\n  margin: 0 auto;\n  min-height: 20em;\n  align-items: center;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-evenly;\n}\n.board-container .board {\n  width: fit-content;\n  margin: 0 auto;\n  position: relative;\n  text-align: center;\n}\n.board-container .board .topNums {\n  color: aquamarine;\n  position: relative;\n  display: flex;\n  top: 0;\n  border: 2px solid transparent;\n}\n.board-container .board .topNums > div {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-end;\n  width: 1rem;\n  padding: 0.2rem 0.5rem;\n  border: 1px solid transparent;\n}\n.board-container .board .leftNums {\n  color: aquamarine;\n  position: absolute;\n  left: 0;\n  transform: translate(-105%);\n  display: flex;\n  flex-direction: column;\n  border: 2px solid transparent;\n}\n.board-container .board .leftNums > div {\n  display: flex;\n  justify-content: flex-end;\n  align-items: center;\n  height: 1rem;\n  padding: 0.5rem 0.2rem;\n  border: 1px solid transparent;\n}\n.board-container .board .grid {\n  border: 2px solid black;\n  margin-bottom: 2em;\n}\n.board-container .board .grid .row-div {\n  display: flex;\n}\n.board-container .board .grid .row-div:nth-child(5n+0) {\n  border-bottom: 2px solid black;\n}\n.board-container .board .grid .row-div:last-child {\n  border-bottom: none;\n}\n.board-container .board .grid .row-div .square {\n  width: 2rem;\n  height: 2rem;\n  border: 1px outset black;\n}\n.board-container .board .grid .row-div .square:hover {\n  filter: brightness(120%);\n}\n.board-container .board .grid .row-div .square:nth-child(5n+0) {\n  border-right: 3px solid black;\n}\n.board-container .board .grid .row-div .square:last-child {\n  border-right: 1px outset black;\n}\n.board-container .board .grid .row-div .unclicked {\n  background-color: aquamarine;\n}\n.board-container .board .grid .row-div .filled {\n  background-color: teal;\n}\n.board-container .board .grid .row-div .exed {\n  background-color: aquamarine;\n  background-image: url(../../images/X.png);\n  background-position: center;\n  background-size: cover;\n}\n.board-container .board .grid .row-div .maybe {\n  background-color: aquamarine;\n  background-image: url(../../images/question_mark.png);\n  background-position: center;\n  background-size: 2rem 2rem;\n}\n.board-container .board .grid.cursor {\n  cursor: default;\n}\n.board-container .board .grid.x-mode {\n  cursor: no-drop;\n}\n.board-container .board .grid.maybe-mode {\n  cursor: help;\n}\n.board-container #board-modal {\n  width: 100%;\n  height: 100%;\n  z-index: 4;\n  position: absolute;\n  background-color: #5481bd;\n  opacity: 0.5;\n  left: 0;\n  top: 0;\n}\n.board-container .hidden {\n  display: none;\n}\n.board-container .active {\n  display: block;\n}\n.board-container .button-box {\n  display: flex;\n}\n.board-container .button-box .pressed {\n  border: 2px inset #e6cacf;\n}\n.board-container .button-box .unpressed {\n  border: 2px outset #e6cacf;\n}\n.board-container .button-box #cursor-button {\n  width: 2em;\n  height: 2em;\n  background-color: #93b53c;\n  margin: 1em;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.board-container .button-box #cursor-button:hover {\n  filter: brightness(120%);\n}\n.board-container .button-box #x-button {\n  width: 2em;\n  height: 2em;\n  background-color: #db2e2e;\n  margin: 1em;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.board-container .button-box #x-button:hover {\n  filter: brightness(120%);\n}\n.board-container .button-box #maybe-button {\n  width: 2em;\n  height: 2em;\n  background-color: pink;\n  margin: 1em;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.board-container .button-box #maybe-button:hover {\n  filter: brightness(120%);\n}\n\n.message-box {\n  color: whitesmoke;\n  font-size: 16px;\n  z-index: 10;\n  position: relative;\n  border: 1px solid #2f4a62;\n  border-radius: 0.5em;\n  background-color: #2f4a62;\n  box-shadow: 2px 2px 2px #2f4a62;\n  font-family: \"Kiwi Maru\";\n  width: 20rem;\n  height: 10rem;\n  margin: 3rem auto;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n.message-box .outer-timer-box {\n  display: flex;\n  align-items: center;\n}\n.message-box .outer-timer-box .pause-box {\n  padding: 0.5em;\n  border: 3